body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.mainheader {
  display: flex;
  justify-content: space-between;
  padding: 20px 60px;
  align-items: center;
  height: 70px;
  margin-bottom: 10px;
  position: fixed;
  width: 100%;
  top: 0;
  background-color: #fff;
  position: fixed;
  padding-bottom: 30px;
}

.links {
  display: inline-block;
  align-items: center;
  justify-content: center;
  margin-right: 80px;

}

.links a {
  margin: 20px;
  text-decoration: none;
  font-weight: 700;
  font-size: 21px;
  color: #202F42;
}

.links a:hover {
  border-bottom: 2px solid #1731db;
  border-top: 2px solid #1731db;
  transition: all 0.2s linear;
  padding: 3px;

}

.grey {
  width: 20%
}

.maintexth1 {
  font-weight: bolder;
  font-size: 52px;
  background: -webkit-linear-gradient(#505050, #01548b);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: blink 3s linear infinite;
}

.aboutdiv {
  font-size: 20px;
  background-color: #01548b;
  padding: 30px;
  color: white;
  border-radius: 10px;
  text-align: center;
  margin-top: 60px;
  margin: 20px 100px;
}

.aboutdiv p {
  text-align: start;

}

.maintext {
  font-weight: bolder;
  font-size: 52px;
}

.cimage {
  width: 100%;
  height: 400px;
  border-radius: 50px;
}

.maincss {
  display: flex;
  justify-content: space-between;
  padding: 60px;
  align-items: center;
  margin-left: 150px;
}

.cdimage {
  margin-right: 100px;
}

.cdlogo {
  height: 100px;
  margin-left: 12px;
  margin-top: 20px;
  transition: width 1s, height 1s;
}

.cdlogo:hover {
  height: 110px;
}

.description {
  width: 650px;
}

.aboutdiv button {
  padding: 15px 20px;
  background-color: white;
  border: none;
  border-radius: 20px;
  margin-top: 15px;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: large;
  font-weight: bold;
  background-color: white;
  color: #01548b;
  transition: 0.4s;
}

.aboutdiv button:hover {
  color: white;
  background-color: rgb(238, 109, 76);
}

.social {
  text-align: center;
  margin-top: 60px;
}

.social img {
  width: 40px;
  height: 40px;
  padding: 20px;

}

.maindiv {
  margin-top: 130px;
}

.headempty {
  background-color: #01548b;
  height: 20px;
  width: 500px;
  top: 50px;
  left: 50px;
  margin-top: 540px;
  position: absolute;
}

.headempty1 {
  background-color: #01548b;
  height: 420px;
  width: 20px;
  top: 80px;
  left: 50px;
  margin-top: 90px;
  margin-right: 30px;
  position: absolute;
}

.empty1 {
  background-color: #01548b;
  height: 300px;
  margin-top: 130px;
}

.aboutimage img {
  width: 360px;
  height: 290px;
  border-radius: 10%;
  margin-top: -170px;

}

.aboutme p {
  font-size: large;
  font-weight: 600;
}

.aboutme h1 {
  color: #01548b;
}

.aboutimage {
  text-align: center;
}

.aboutme {
  background-color: rgb(233, 230, 230);
  margin: 20px 190px;
  padding: 30px;
  border-radius: 50px;
}

.skillsh1 {
  color: #01548b;
  margin-top: 130px;
  text-align: center;
}

.skillsmain {
  display: grid;
  grid-template-columns: auto auto auto;
  padding: 20px;
  justify-content: center;
  align-items: center;
}

.skillsdiv {
  background-color: white(233, 230, 230);
  height: 220px;
  width: 280px;
  padding-top: 20px;
  margin: 50px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 10px 7px 7px rgb(201, 193, 193);
  border: 1px rgb(196, 189, 189) solid;
}

.mainproject {
  display: grid;
  grid-template-columns: auto auto;
  padding: 20px;
  justify-content: center;
  align-items: center;
}

.projectsection {
  background-color: white(233, 230, 230);
  height: 250px;
  width: 480px;
  padding-top: 20px;
  margin: 50px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 10px 7px 7px rgb(201, 193, 193);
  border: 1px rgb(196, 189, 189) solid;

}

.skillsdiv h2 {
  color: #01548b;
}

.skillsdiv img {
  text-align: center;
  width: 100px;
  margin-top: 15px;
}

.projectsection img {
  text-align: center;
  width: 100px;
  margin-top: 15px;
}

.projectsection button {
  padding: 15px 15px;
  border: none;
  border-radius: 5px;
  margin-top: 45px;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: medium;
  font-weight: bold;
  background-color: #01548b;
  color: white;
  transition: width 50s, height 7s;
}

.projectsection button:hover {
  color: white;
  background-color: rgb(238, 109, 76);
  height: 55px;
  width: 160px;
}

.maineducationdiv {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  margin-left: 70px;
  margin-bottom: 50px;
}

.mained {
  display: flex;
  flex-direction: row;

}

.educationdiv {
  height: 220px;
  width: 100%;
  text-align: start;
  margin-top: 50px;
  padding: 20px;
  border-radius: 20px;
  box-shadow: 10px 7px 7px rgb(201, 193, 193);
  border: 1px rgb(196, 189, 189) solid;

}

.contactsocial {
  text-align: center;
  margin-left: 100px;
}

.card {
  box-shadow: 10px 7px 7px rgb(201, 193, 193);
  border: 1px rgb(196, 189, 189) solid;
  transition: 0.3s;
  padding: 35px;
  width: 90%;
  background-color: white;
  border-radius: 20px;
}

.formcard {
  box-shadow: 10px 7px 7px rgb(201, 193, 193);
   border: 1px rgb(196, 189, 189) solid;
  transition: 0.3s;
  padding: 20px 40px;
  background-color: #01548b;
  border-radius: 20px;
}

.contactsocial img {
  width: 40px;
  height: 40px;
  padding: 20px;

}

.contactsocial h3 {
  text-align: start;
  margin: 15px;
}

.vl {
  border-left: 6px solid #01548b;
  border-top: 6px #01548b solid;
  height: 270px;
  margin-left: 300px;
  margin-top: 55px;

}

.vlr {
  border-left: 26px solid #01548b;
  height: 20px;
  margin-left: 290px;
}

.vl2 {
  border-left: 6px solid #01548b;
  height: 290px;
  margin-left: 300px;

}

.vl3 {
  border-left: 6px solid #01548b;
  height: 300px;
  margin-left: 300px;

}

.vl4 {
  border-left: 6px solid #01548b;
  border-bottom: #01548b 6px solid;
